module.exports = angular.module('directives', []).directive('ngtimeone', ['$rootScope', function () {
    /* 日期选择插件1*/
    return {
        // A 是单词 attribute 首字母，将指令当属性来用
        // E 是单词 element   首字母，将指令当标签来用
        // C 是单词 class     首字母，将指令当类来用
        // M 是单词 mark      首字母，将指令将注释来用
        restrict: 'A',
        link: function ($rootScope, ele, attrs, ngModel) {
            $(".timeone").datetimepicker({
                changeMonth: true,
                changeYear: true,
                dateFormat: "yy-mm-dd",
                showHour: true,
                showMinute: true,
                showSecond: false,
                timeFormat: 'HH:mm',
                stepHour: 1,
                stepMinute: 1,
                stepSecond: 1,
                minDate: '2017-04-01 00:00:00',
                maxDate: new Date(),
                onSelect: function (dateText) {
                    console.log(dateText)
                }
            });
        }
    };
}]).name


