'use strict';
// 这里加载样式文件可以单独打包出去
require ('./page1/page1.less');

module.exports = angular.module('app.home',[]).config(function($stateProvider) {
    $stateProvider.state('home', {
        url: '/home',
        templateProvider: function($q) {
            var deferred = $q.defer();
            require.ensure(['./home.html'], function(require) {
                var template = require('./home.html');
                deferred.resolve(template);
            }, 'home-tpl');
            return deferred.promise;
        },
        controller: 'homeCtrl',
        controllerAs: 'vm',
        resolve: {
            'app.home': function($q, $ocLazyLoad) {
                var deferred = $q.defer();
                require.ensure(['./home.js'], function() {
                    var mod = require('./home.js');
                    $ocLazyLoad.load({
                        name: 'app.home'
                    });
                    deferred.resolve([mod.controller]);
                }, 'home-ctl');
                return deferred.promise;
            }
        }
    })
    .state('home.page1', {
        url: '/page1',
        templateProvider: function($q) {
            var deferred = $q.defer();
            require.ensure(['./page1/page1.html'], function(require) {
                var template = require('./page1/page1.html');
                deferred.resolve(template);
            }, 'homePage1-tpl');
            return deferred.promise;
        },
        controller: 'homePage1Ctrl',
        controllerAs: 'page1',
        resolve: {
            'app.home': function($q, $ocLazyLoad) {
                var deferred = $q.defer();
                require.ensure(['./page1/page1.js'], function() {
                    var mod = require('./page1/page1.js');
                    $ocLazyLoad.load({
                        name: 'app.home'
                    });
                    deferred.resolve([mod.controller]);
                }, 'homePage1-tpl');
                return deferred.promise;
            }
        }
    })
    .state('home.page2', {
        url: '/page2',
        templateProvider: function($q) {
            var deferred = $q.defer();
            require.ensure(['./page2/page2.html'], function(require) {
                var template = require('./page2/page2.html');
                deferred.resolve(template);
            }, 'homePage2-tpl');
            return deferred.promise;
        },
        controller: 'homePage2Ctrl',
        controllerAs: 'page2',
        resolve: {
            'app.home': function($q, $ocLazyLoad) {
                var deferred = $q.defer();
                require.ensure(['./page2/page2.js'], function() {
                    var mod = require('./page2/page2.js');
                    $ocLazyLoad.load({
                        name: 'app.home'
                    });
                    deferred.resolve([mod.controller]);
                }, 'homePage2-tpl');
                return deferred.promise;
            }
        }
    });
}).name;



